var graceChecker = require('@/common/graceChecker.js');
export default {
	data() {
		return {
			phone: '',
			code: '',
			pay_paw: '',
			pay_pawa: '',
			second: 90,
			showText: true
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad() {
		var user = uni.getStorageSync('uerInfo');
		this.phone = user.phone;
	},
	methods: {
		sendCode() {
			//发送验证码
			var th = this;
			if (th.phone == '') {
				uni.showToast({
					icon: 'none',
					title: '请填写正确的手机号码'
				});
				return false;
			}

			//发送验证码
			th.$request.common
				.sendCode({
					phone: th.phone,
					type: 2
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						th.showText = false;
						var interval = setInterval(() => {
							let times = --th.second;
							th.second = times < 10 ? '0' + times : times; //小于10秒补 0
						}, 1000);
						setTimeout(() => {
							clearInterval(interval);
							th.second = 90;
							th.showText = true;
						}, 90000);
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		formSubmit(e) {
			var _this = this;
			//定义表单规则
			var rule = [{
					name: 'code',
					checkType: 'string',
					checkRule: '6,6',
					errorMsg: '验证码6个字符'
				},
				{
					name: 'pay_paw',
					checkType: 'string',
					checkRule: '6,20',
					errorMsg: '密码为6-20个字符'
				},
				{
					name: 'pay_pawa',
					checkType: 'same',
					checkRule: _this.pay_paw,
					errorMsg: '两次密码不相同'
				}
			];
			//进行表单检查
			var formData = e.detail.value;

			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				uni.showLoading({
					title: '数据提交中'
				});
				_this.$request.user
					.updPayPsw(formData)
					.then(data => {
						uni.hideLoading();
						if (data.status == 1) {

							uni.showToast({
								icon: 'none',
								title: '修改成功'
							});
							uni.navigateTo({
								url: '/pages/member/details'
							});

						} else {
							uni.showToast({
								icon: 'none',
								title: data.msg
							});
						}
					})
					.catch(err => {
						uni.hideLoading();
						//消息异常
						uni.showToast({
							icon: 'none',
							title: '数据加载异常'
						});
					});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: 'none'
				});
			}
		}
	}
};
